@use "../global/variables.scss";

.contacts-owner {
  padding-top: 20px;
  padding-bottom: 24px;
  position: relative;

  @media (min-width: variables.$screen-tablet) {
    padding-bottom: 0;
  }
}

.contacts-owner__label {
  position: relative;

  &:first-of-type {
    @media (min-width: variables.$screen-tablet) {
      padding-top: 65px;
    }

    @media (min-width: variables.$screen-desktop) {
      padding-top: 0;
      width: calc(100% / 2);
    }
  }

  &--email {
    &::before {
      content: "";
      display: block;
      position: absolute;
      right: 13.5px;
      mask-image: url("../img/sprite.svg#icon_email");
      background-position: center;
      background-repeat: no-repeat;
      background-color: #222222;
      width: 19px;
      height: 14px;
    }
  }

  &--phone {
    &::before {
      content: "";
      display: block;
      position: absolute;
      right: 14px;
      mask-image: url("../img/sprite.svg#icon_phone");
      background-position: center;
      background-repeat: no-repeat;
      background-color: #222222;
      width: 16px;
      height: 16px;
    }
  }

  &--email:focus-within,
  &--phone:focus-within {
    &::before {
      background-color: #68b738;
    }
  }
}

